
<template>
  <div class="Collection">
    <div class="Collection_Item" v-for="c in Collection" :key="c.pid">
      <router-link :to="`/detail/${c.pid}`">
        <div class="Collection_img">
          <img :src="c.image" />
        </div>
      </router-link>
      <router-link :to="`/detail/${c.pid}`">
        <div class="Collection_info">
          <div class="Collection_text">{{c.store_name}}</div>
          <div class="Collection_pd">
            <div class="Collection_pice">￥{{c.price}}</div>
          </div>
        </div>
      </router-link>
      <div class="Collection_delete">
        <button @click="del">删除</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Collection",
  data() {
    return {
      Collection: [],
    };
  },

  components: {},
  methods: {
    getCollection() {
      this.$axios
        .get("http://47.115.51.185/api/collect/user?page=1&limit=20")
        .then((data) => {
          if (data.data.status == 200) {
            this.Collection = data.data.data;
            console.log(this.Collection);
          } else {
            this.$router.push("/login");
          }
        });
    },
    del() {
      
    },
  },
  created() {
    this.getCollection();
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.Collection {
  background-color: #f5f5f5;
  height: 100vh;
  .Collection_Item {
    background-color: #fff;
    height: 28vw;
    border-bottom: 1px solid #ddd;
    padding: 0 4vw;
    align-items: center;
    display: flex;
    justify-content: space-around;
    position: relative;
    .Collection_img {
      height: 20vw;
      width: 20vw;
      border-radius: 1.5vw;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .Collection_info {
      width: 65vw;
      .Collection_text {
        font-size: 4vw;
        width: 100%;
        color: #000;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      .Collection_pd {
        display: flex;
        margin-top: 8vw;
        .Collection_pice {
          font-size: 4vw;
          color: red;
        }
      }
    }
    .Collection_delete {
        position: absolute;
        bottom: 4vw;
        right: 4vw;
    }
    &:last-of-type {
      border-bottom: none;
    }
  }
}
</style>